<template>
  <div class="music-list" ref="musicList">
    <div class="back" @click="back">
      <i class="icon-back iconfont">&#xe662;</i>
    </div>
    <h1 v-html="title" class="title"></h1>
    <div class="bg-image" :style="bgStyle" ref="bgImage">
      <div class="filter"></div>
      <div class="play-wrapper">
        <div class="play" ref="play" v-show="songs.length" @click="ranPlay">
          <i class="iconfont">&#xe627;</i>
          <span>随机播放全部</span>
        </div>
      </div>
    </div>
    <div class="layer" ref="layer"></div>
    <scroll :data="songs" class="list" ref="list" :probeType="probeType" :listenScroll="listenScroll" @scroll="scroll">
      <div class="song-list-wrapper">
        <song-list :songs="songs" @select="selectItem" :rank="rank"></song-list>
      </div>
      <div class="loading-wrapper" v-show="!songs.length">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>

<script>
import Scroll from '@/base/scroll/scroll'
import SongList from '@/base/song-list/song-list'
import {prefixStyle} from '@/common/js/dom'
import Loading from '@/base/loading/loading'
import {mapActions} from 'vuex'
import {playlistMixin} from '@/common/js/mixin'
const transform = prefixStyle('transform')
const TITLE_HEIGHT = 40
export default {
  mixins:[playlistMixin],
  props:{
    songs: {
      type: Array,
      default: []
    },
    title: {
      type: String,
      default: ''
    },
    image: {
      type: String,
      default: ''
    },
    rank: {
      type: Boolean,
      default: false
    }
  },
  data(){
    return {
      probeType: 3,
      listenScroll: true,
      scrollY: 0
    }
  },
  mounted() {
    this.imageHeight = this.$refs.bgImage.clientHeight
    this.minScroll = - this.$refs.bgImage.clientHeight + TITLE_HEIGHT
    this.$refs.list.$el.style.top = `${this.imageHeight}px`
  },
  methods:{
    back(){
      this.$router.back()
    },
    scroll(pos){
      this.scrollY = pos.y
    },
    selectItem(song,index){
      this.selectPlay({
        list:this.songs,
        index:index
      })
    },
    ranPlay(){
      this.randomPlay({
        list:this.songs
      })
    },
    handlePlaylist(list){
      const bottom = list.length > 0 ? '60px' : ''
      this.$refs.musicList.style.bottom = bottom
      this.$refs.list.refresh()
    },
    ...mapActions([
        'selectPlay',
        'randomPlay'
      ])
  },
  computed: {
    bgStyle() {
      return `background-image:url(${this.image})`
    }
  },
  watch:{
    scrollY(newY){
      let translateY = Math.max(this.minScroll,newY)
      let zIndex = 0
      let scale = 1
      let percent = Math.abs(newY/this.imageHeight)

      //遮层跟随往上滑动
      this.$refs.layer.style[transform] = `translate3d(0,${translateY}px,0)`

      //往下滑动图片放大
      if(newY > 0){
        scale = 1 + percent
        zIndex = 10
      }
      this.$refs.bgImage.style[transform] = `scale(${scale})`

      //防止文字覆盖标题
      if( newY < this.minScroll ){
        zIndex = 10
        this.$refs.bgImage.style.paddingTop = 0
        this.$refs.bgImage.style.height = `${TITLE_HEIGHT}px`
        this.$refs.play.style.display = 'none'
      }else {
        this.$refs.bgImage.style.paddingTop = '70%'
        this.$refs.bgImage.style.height = 0
        this.$refs.play.style.display = ''
      }
      this.$refs.bgImage.style.zIndex = zIndex
    }
  },
  components: {
    Scroll,
    SongList,
    Loading
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~@/common/stylus/var"
  .music-list
    position: fixed
    z-index: 100
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: $color-white
    .back
      position absolute
      top 0
      left 6px
      z-index 50
      .icon-back
        display block
        padding 10px
        font-size: $font-size-large-x
        color $color-theme-l
    .title
      position: absolute
      top: 0
      left: 10%
      z-index: 40
      width: 80%
      text-align: center
      line-height: 40px
      font-size: $font-size-large
      color: $color-white
      no-wrap()
    .bg-image
      position: relative
      width: 100%
      height: 0
      padding-top: 70%
      transform-origin: top
      background-size: cover
      .play-wrapper
        position: absolute
        bottom: 20px
        z-index: 50
        width: 100%
        .play
          box-sizing: border-box
          width: 135px
          padding: 7px 0
          margin: 0 auto
          text-align: center
          border: 1px solid $color-theme-l
          color: $color-theme-l
          border-radius: 100px
          .icon-play
            display: inline-block
            vertical-align: middle
            margin-right: 6px
            font-size: $font-size-medium-x
          .text
            display: inline-block
            vertical-align: middle
            font-size: $font-size-small
      .filter
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgba(7, 17, 27, 0.4)
    .layer
      position relative
      height 100%
      background-color $color-white
    .list
      position: absolute
      top: 0
      bottom: 0
      width: 100%
      background: $color-white
      .song-list-wrapper
        padding: 20px 30px
      .loading-wrapper
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)    
</style>



